@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: local("Oswald"), url("/fonts/oswaldregular.woff2") format("woff2"), url("/fonts/oswaldregular.woff") format("woff"); }

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 600;
  src: local("Oswald"), url("/fonts/oswaldmedium.woff2") format("woff2"), url("/fonts/oswaldmedium.woff") format("woff"); }

*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

li {
  list-style: none; }

.visually__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header__logo:hover,
.page-header__logo:focus {
  opacity: 0.8; }

.page-header__logo:active {
  opacity: 0.6; }

.site-list__item a {
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase; }

.site-list__item a:hover,
.site-list__item a:focus {
  opacity: 0.6; }

.site-list__item a:active {
  opacity: 0.3; }

.site-list__item--active {
  border-bottom: 2px solid #68B738; }

.slogan__header h2 {
  font-weight: 400;
  font-size: 60px;
  line-height: 60px;
  color: #ffffff; }

.slogan__intro p,
.slogan__to-all {
  font-weight: 400;
  font-size: 14px;
  line-height: 14px; }

.slogan__to-all {
  padding: 7px 44px; }

.slogan__to-all:hover,
.slogan__to-all:focus {
  background-color: #5EAA2F; }

.slogan__to-all:active {
  background-color: #5EAA2F;
  opacity: 0.3; }

.programs__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  color: #000000;
  text-transform: uppercase; }

.programs__description {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444; }

.programs__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #000000; }

.programs__link:active {
  opacity: 0.3; }

.functioning__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000; }

.functioning__item {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444; }

.example__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000; }

.example__description {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444; }

.example__value {
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000; }

.examples__description {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #444444; }

.example__cost {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #000000; }

.contacts__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #000000; }

.contacts__text {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #444444; }

.copyright__link {
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444; }

.page-title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #000000; }

.page-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #000000; }

.personal-data__label,
.personal-data__input {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444; }

.personal-data__input {
  color: #000000;
  border: 1px solid #E7E7E7; }

.personal-data__input:hover {
  border: 1px solid #CDCDCD; }

.personal-data__input:focus {
  border: 2px solid #444444; }

.personal-data__input:invalid {
  border: 2px solid #FF8282; }

.purpose__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222; }

.purpose__input {
  color: #000000;
  border: 1px solid #CDCDCD; }

.purpose__input:hover {
  border: 1px solid #BABABA; }

.purpose__item:disabled {
  opacity: 0.3; }

.contacts-forms__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: #000000; }

.contacts-forms__label,
.contacts-forms__input {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #000000; }

.contacts-forms__input {
  border: 1px solid #CDCDCD; }

.personal-data__input:disabled {
  border: 2px solid #444444; }

.personal-data__input:invalid {
  border: 2px solid #FF8282; }

.comments__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: #000000; }

.comments__input {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #000000; }

.additionally__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: #000000; }

.additionally__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #000000; }

.additionally__input {
  color: #000000;
  border: 1px solid #CDCDCD; }

.additionally__input:hover {
  border: 1px solid #BABABA; }

.additionally__item:disabled {
  opacity: 0.3; }

.additionally__submit {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding: 7px 44px; }

.additionally__submit:hover,
.additionally__submit:focus {
  background-color: #5EAA2F; }

.additionally__submit:active {
  background-color: #5EAA2F;
  opacity: 0.3; }

.additionally__legend--bottom {
  font-weight: 400;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #000000; }

.button {
  display: inline-block;
  text-align: center;
  background-color: #68B738;
  color: #ffffff;
  text-transform: uppercase;
  border: none;
  vertical-align: middle; }
